.about-supplier-container {
  width: 100%;
}

.about-supplier-header {
  display: flex;
  align-items: center;
  padding: 15px 25px;
  background-color: #f5f5f5;
}

.about-supplier-logo {
  flex-grow: 1;
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.about-supplier-nav-links a {
  margin-right: 20px;
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.about-supplier-breadcrumbs {
  margin: 20px 0;
  padding-left: 25px;
  font-size: 15px;
}

.about-supplier-content {
  display: flex;
  padding: 0 25px;
}

.about-supplier-left-section {
  flex: 1;
  margin-right: 20px;
}

.about-supplier-product-image img {
  width: 100%;
  height: auto;
}

.about-supplier-follow-button {
  margin: 10px 0;
  padding: 10px 20px;
  background-image: linear-gradient(rgba(116, 204, 126, 1), rgba(0, 129, 138, 1));
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.about-supplier-details {
  padding: 10px 0;
}

.about-supplier-title {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
}

.about-supplier-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.about-supplier-label {
  font-weight: bold;
  color: #333;
}

.about-supplier-value {
  color: #666;
}

.about-supplier-inquiry-button {
  padding: 10px 20px;
  background-image: linear-gradient(rgba(116, 204, 126, 1), rgba(0, 129, 138, 1));
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  margin-top: 20px;
}

.about-supplier-right-section {
  flex: 2;
  margin-left: 20px;
}

.about-supplier-about {
  margin-bottom: 20px;
}

.about-supplier-description {
  color: #666;
}

.about-supplier-more-products {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.about-supplier-product-card {
  width: 30%;
  margin-bottom: 20px;
}

.about-supplier-product-card img {
  width: 100%;
  height: auto;
}

.about-supplier-product-card .about-supplier-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.about-supplier-product-card .about-supplier-details {
  margin: 10px 0;
}

.about-supplier-price {
  font-size: 16px;
  color: #007bff;
}

.about-supplier-additional-sections {
  padding: 0 25px;
}

.about-supplier-awards, .about-supplier-certificate, .about-supplier-vacancy {
  margin: 20px 0;
  /* background-color: #007bff; */
  margin-right: 50px;
}

.about-supplier-award-row, .about-supplier-certificate-row, .about-supplier-vacancy-row {
  display: flex;
  justify-content: space-between;
}

.about-supplier-award, .about-supplier-certificate-item, .about-supplier-vacancy-item {
  width: 30%;
}

.about-supplier-award img, .about-supplier-certificate-item img {
  width: 100%;
  height: auto;
}

.about-supplier-description .about-supplier-label, .about-supplier-description .about-supplier-value {
  display: block;
  margin: 5px 0;
}
