.certificate {
  margin-bottom: 20px;
  width: auto;
  margin-left : 30px;
  margin-right: 200px;
  margin-top: 40px;
}

.certificate .title {
  font-weight: bold;
font-weight: bold;
font-weight: bold;
  margin-bottom: 20px;
  margin-left : 30px;
  /* margin-bottom: 20px;
  margin-left : 30px; */
}

.certificate .award-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.certificate .award {
  width: 30%;
  text-align: center;
}

.certificate .award img {
  width: 100%;
  max-height: 150px;
  object-fit: cover;
  margin-bottom: 10px;
}

.reviews {
  margin-bottom: 20px;
  width: auto;
  margin-top: 40px;
  margin-left : 30px;
  margin-right: 20px;
  
}

.reviews .title {
  font-weight: bold;
  /* margin-bottom: 30px;
  margin-top: 40px; */
  /* margin-left: 10px; */
}

.review {
  display: flex;
  margin-bottom: 20px;
  
  margin-left: 20px;

}

.user-image {
  width: 80px;
  height: 80px;
  margin-right: 20px;
  margin-top: 1%;
}

.user-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  margin-top: 10px;
}

.review-content {
  flex: 1;
  margin-top: 30px;
}

.review-content .name {
  font-weight: bold;
  margin-bottom: 5px;
}

.review-content .date {
  color: #666;
  margin-bottom: 10px;
}

.review-content .title {
  font-weight: bold;
  margin-bottom: 5px;
}

.review-content .rating {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.review-content .stars {
  color: #ffc107;
  margin-left: 5px;
}

.review-content .description {
  color: #666;
}

.right-section {
  width: 65%;
  
}

.about {
  margin-bottom: 40px;
}

.about .title {
  font-weight: bold;
  margin-bottom: 10px;
}

.about .description {
  color: #131111;
  width: auto;
  margin-right: 50px;
}

.more-products {
  margin-bottom: 4  0px;
  margin-top: 40px;
  width: auto;
  overflow: hidden;
  margin-right: 55px;
}

.more-products .title {
  font-weight: bold;
  margin-bottom: 10px;
}

.product-card {
  width: 100;
  border: 5px solid #0f0d0d;
  padding: 10px;
  margin-bottom: 10px;
  z-index: 99999999999 ;
  /* position: absolute; */
}

.product-card img {
  width: auto;
  height: 150px;
  object-fit: cover;
  margin-bottom: 10px;
}

.product-card .title {
  font-weight: bold;
  margin-bottom: 5px;
}

.product-card .details {
  color: #666;
}

.product-card .details .label {
  font-weight: bold;
  margin-bottom: 5px;
}

.product-card .details .value {
  margin-bottom: 5px;
}

.product-card .details .price {
  font-weight: bold;
  margin-bottom: 5px;
}
