body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column; /* Adjust layout for mobile */
  }
  
  .app-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
    overflow-x: hidden; /* Prevent horizontal scrolling */
  }
  
  .sidebar {
    width: 343px;
    height: 1085px;
    margin: 20px;
    border: 1px solid #ddd;
    border-radius: 20px;
    padding: 20px;
    box-sizing: border-box;
    background-color: #fff;
  }
  
  .main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 202px);
    background-color: transparent;
    box-sizing: border-box;
  }
  
  .shoe-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 columns for the shoe grid */
    gap: 10px;
    justify-content: left;
    width: auto;
    /* background-color: #007bff; */
    margin-left: 30%;
    position: relative;
    margin-right: 20px;
    margin-top: -50%;
  }
  
  .shoe-card {
    background: #fff;
    border-radius: 16px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    margin: 10px; /* Adjusted margin for spacing */
    padding: 10px;
    height: 314px; /* Fixed height for consistency */
    /* position: relative; */
    text-align: center;
    box-sizing: border-box;
    width: 269px;
  }
  
  .shoe-image-container {
    width: 80%;
    height: 150px;
    overflow: hidden;
    border-radius: 16px;
  }
  
  .shoe-image {
    width: 100%;
    height: 80%;
    object-fit: cover;
    border-radius: 16px;
  }
  
