  .shoe-info {
    padding-top: 10px;
    width: 100%;
  }
  
  .shoe-info h4 {
    font-size: 1em;
    margin-bottom: 6px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  .shoe-info .brand {
    color: #00aaff;
    font-size: 0.9em;
    margin-bottom: 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  .type-price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
    width: 100%;
  }
  
  .shoe-info .type {
    font-size: 0.9em;
    color: #999;
    margin: 0;
  }
  
  .shoe-info .price {
    font-size: 1em;
    color: #333;
    font-weight: bold;
    margin: 0;
  }
  
  hr {
    border: 0;
    height: 1px;
    background: #eee;
    margin: 6px 0;
    
  }
  .categories-header {
    padding: 10px;
    
    
  }
  
  .categories-header ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .categories-header li {
    display: flex;
    align-items: center;
    
  }
  
  .categories-header h3 {
    margin: 0;
    margin-right: 10px; /* Adjust as needed */
  }
  
  .category-select {
    padding: 5px;
    font-size: 16px;
    border-radius: 5px;
    background-color: linear-gradient(rgba(116, 204, 126, 1), rgba(0, 129, 138, 1)) ;;
  }
  
  
  .category-info {
    display: flex;
    justify-content: space-between;
    font-size: 0.8em;
    color: #666;
    padding-top: 8px;
    background-color: #f1f3f5;
    
  }
  
  .category-info span {
    flex: 1;
    text-align: center;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 50px;
    /* align-content: center; */
    /* align-items: center; */
  }
  
  .pagination span {
    display: inline-block;
    padding: 8px 12px;
    margin: 0 5px;
    border-radius: 50%;
    cursor: pointer;
  }
  
  .pagination span.active {
    background-color: #ffffff;
    color: black;
    box-shadow: 0 5px 7px rgba(27, 171, 54, 0.3);
  }
  
  .category-card {
    width: 565px;
    height: 238px;
    margin: 20px auto;
    border: 1px solid #ddd;
    border-radius: 20px;
    background-color: #fff;
