  @media (max-width: 1024px) {
    .shoe-cards {
      grid-template-columns: repeat(2, 1fr); /* 2 columns for medium screens */
    }
  }
  
  @media (max-width: 768px) {
    .app-container {
      flex-direction: column;
      align-items: center;
    }
  
    .sidebar {
      width: 100%;
      margin: 10px;
    }
  
    .main-content {
      width: 100%;
      padding: 10px;
    }
  
    .category-card {
      width: 100%;
      margin-bottom: 20px;
    }
  
    .shoe-cards {
      grid-template-columns: repeat(1, 1fr); /* 1 column for small screens */
    }
  
    .small-cards {
      position: relative;
      right: 0;
      top: 0;
      transform: none;
    }
  }
  
  @media (max-width: 480px) {
    .footer-links {
      flex-direction: column;
      align-items: center;
    }
  
    .footer-links .links,
    .footer-links .oc-expert,
    .footer-links .connect {
      width: 100%;
      text-align: center;
    }
  
    .header {
      flex-direction: column;
      align-items: center;
    }
  
    nav ul {
      flex-direction: column;
      align-items: center;
    }
  
    .category-select
     {
      width: 100%;
      max-width: none;
      display: flex;
      margin-left: 50px;
    }

    .search-container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
    }
    
    .search-form {
      display: flex;
      align-items: center;
      border: 1px solid #dfe1e5;
      border-radius: 24px;
      padding: 10px;
      box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
      max-width: 584px;
      width: 100%;
    }
    
    .search-bar {
      width: 100%;
      border: none;
      outline: none;
      font-size: 16px;
      padding: 0 10px;
    }
    
    .search-button {
      background: none;
      border: none;
      outline: none;
      cursor: pointer;
      padding: 0 10px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    
    .search-icon {
      fill: #f7f8f8;
      width: 24px;
      height: 24px;
    }
    
    .search-bar::placeholder {
      color: #dfe1e5;
    }
    
  
    .shoe-cards {
      grid-template-columns: repeat(1, 1fr); /* 1 column for extra small screens */
    }
  }
  